import { useState, useEffect, Fragment } from 'react';
import { Row, Col, Button, message, Space, Table, Input, Form, Divider } from 'antd';
import Author from '../../compoent/Author/Author';
import { useToken } from '../../provider/tokenProvider';
import { useNavigate } from 'react-router-dom';
import ajax from '../../provider/ajax.jsx';
import './Home.less';

const { Column } = Table;

const Home = () => {
    const { setToken } = useToken();//Token
    const navigate = useNavigate();//
    const [userList, setUserList] = useState([]);//
    const [username, setUsername] = useState(null);//
    const [userpassword, setUserpassword] = useState(null);//
    const [newpassword, setNewpassword] = useState(null);//

    useEffect(() => {
        console.log("useEffect userList");
        console.log(userList);
    }, [userList]);

    const checkToken = () => {//Token
        ajax("/jwtmessage", {}, 'GET')
            .then(res => {
                if (res.code === 2000) {
                    message.success(res.msg);
                }
                else {
                    message.error(res.msg);
                }
            });
    }
    const changeToken = () => {//Token
        setToken("1234567890");
        message.success("Token");
    }
    const logOut = () => {//
        message.success("");
        setTimeout(() => { setToken(null) }, 0);
        navigate("/login", { replace: true });
    }
    const destoryUser = () => {//
        ajax("/jwtdestory", {}, 'GET')
            .then(res => {
                if (res.code === 2000) {
                    message.success(res.msg);
                    navigate("/login", { replace: true });
                }
                else {
                    message.error(res.msg);
                }
                setTimeout(() => { setToken(null) }, 0);
            });
    }
    const getUser = () => {//
        ajax("/jwtqueryUser", {}, 'GET')
            .then(res => {
                if (res.code === 2000) {
                    setUserList(res.data);
                    message.success(res.msg);
                }
                else {
                    message.error(res.msg);
                }
            });
    }
    const deleteUser = (record) => {//
        ajax("/jwtdeleteUser", record, 'POST').then(res => {
            if (res.code === 2000) {
                message.success(res.msg);
                setUserList(userList.filter(item => item.username !== record.username));
            }
            else {
                message.error(res.msg);
            }
        });
    }

    const changePassword = () => {//
        let user = {
            username: username,
            password: userpassword,
            newpassword: newpassword
        };

        ajax("/jwtupdateUser", user, 'POST').then(res => {
            if (res.code === 2000) {
                message.success(res.msg);

            }
            else {
                message.error(res.msg);
            }
        }
        );
    }

    return (
        <>
            <div className='home'>
                <Row className="comm-main" type="flex" justify="center">
                    <Col className='comm-left'>
                        <Button className="log-out" onClick={checkToken}>Token</Button>
                        <Button className="log-out" onClick={changeToken}>Token</Button>
                        <Button className="log-out" onClick={logOut}></Button>
                        <Button danger className="log-out" onClick={destoryUser}></Button>
                        <Divider></Divider>
                        <Form onFinish={changePassword}>
                            <Form.Item name="userName" rules={[{ required: true, message: '!' }]}>
                                <Input
                                    type="text"
                                    placeholder="username"
                                    onChange={(e) => setUsername(e.target.value)}
                                />
                            </Form.Item>
                            <Form.Item name="userOldPassword" rules={[{ required: true, message: '!' }]} >
                                <Input.Password
                                    type="password"
                                    placeholder="old password"
                                    onChange={(e) => setUserpassword(e.target.value)}
                                />
                            </Form.Item>
                            <Form.Item name="userNewPassword" rules={[{ required: true, message: '!' }]}>
                                <Input.Password
                                    type="password"
                                    placeholder="new password"
                                    onChange={(e) => setNewpassword(e.target.value)}
                                />
                            </Form.Item>
                            <Form.Item>
                                <Button
                                    className="log-out"
                                    type="primary"
                                    htmlType="submit"
                                >
                                    
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                    <Col className='comm-right'>
                        <Author />
                        <Button className="log-out" onClick={getUser}></Button>
                        <Table dataSource={userList}>
                            <Column title="User-Name" dataIndex="username" key="username" />
                            <Column
                                title="Usear-Password"
                                dataIndex="password"
                                key="password"
                            />
                            <Column
                                title="Action"
                                key="action"
                                render={(_, record) => (
                                    <Space size="middle">
                                        <Button danger size="small" onClick={() => (deleteUser(record))}>Delete</Button>
                                    </Space>
                                )}
                            />
                        </Table>

                    </Col>
                </Row>
            </div >
        </>

    )
}

export default Home;