import { Navigate, Outlet } from "react-router-dom";
import { useToken } from "../provider/tokenProvider";
import { message } from "antd";

export const ProtectedRoute = () => {
    const { token } = useToken();

    // 
    if (!token) {
        // 
        message.error("");
        return <Navigate to="/login" />;
    }

    // 
    return <Outlet />;
};
